html, body {
  height: 100%;
  margin: 0;
  padding: 0;
  color: #acadbf;
  /* background: linear-gradient(180deg, #6a3ffc, #111762); */
  background-color: #101418;
}
*{
  box-sizing: border-box;
}

html {
  display: flex;
  flex-direction: column;
}

body {
  flex: 1;
  display: flex;
  flex-direction: column;
}

#root {
  flex: 1;
  display: flex;
  flex-direction: column;
}

.red {
  color: red;
}

.whiteText,
.whiteText p {
  color: #acadbf !important;
}

.blur {
  backdrop-filter: blur(8px);
  background-color: rgba(0, 0, 0, 0.3);
}
